.form-group {
  display: grid;
  grid-template-rows: 10px 10px;
  grid-template-columns: 1fr 1fr; /* Use the appropriate column widths */
  gap: 0px; /* Set the gap (spacing) between the grid items (fields) to 0 to remove the gap */
  margin-top: 0.5%;
  margin-left: 10px;
  margin: 0.1%;
  padding: 1.8px;
  justify-content: start;
  font-size: 16px;
}

.form-group label {
  text-align: left; /* Align the text within labels to the left */
  margin-left: 50px; /* Add left margin for space */
  margin-right: 5px;
  
}

.form-group input,
.form-group select {
  width: 100%; /* Expand the input/select fields to fill the grid cell */
  padding: 5px; /* Add padding for better visual appearance */
  box-sizing: border-box; /* Include padding in the width calculation */
}





.HTML_Part {
  display: grid;
  grid-template-rows: 10px 10px;
  grid-template-columns: 70fr 30fr; /* Increase the length of the output box */
  gap: 2px; /* Set the gap (spacing) between the grid items (fields) */
  margin-top: 0.5%;
  margin-left: 50px; /* Push the grid to the right side */
  margin-right: 1px; /* Specify the margin value correctly */
  justify-content: start; /* Align grid items to the end (right side) */
  text-align: left; /* Align text within the grid items to the right */
}



